.list {
  width: 1400px;
  height: 1000px;
  margin: auto;
  .formHeader {
    padding: 30px 0;
    width: 100%;
    border-bottom: 1px solid #ddd;
  }
  .f_title {
    font-size: 20px;
    color: #2c2c2c;
    font-weight: bold;
    padding: 10px 0 20px 0;
  }
  .f_table {
    margin-top: 20px;
    .f_top {
      display: flex;
      flex-flow: row nowrap;
      justify-content: space-between;
      align-items: center;
    }
  }
}
